.chat {
  display: flex;
  background-color: #fff;
  width: 1140px;
  height: 100%;
  margin: 0 auto;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);

  &__sidebar {
    width: 325px;
    border-right: 1px solid #ecebeb;
    overflow: auto;

    &-header {
      display: flex;
      height: 58px;
      border-bottom: 1px solid #ecebeb;
      justify-content: space-around;
      align-items: center;

      span.anticon {
        transition: 0.5s ease;
        opacity: 0.5;
        &:hover {
          opacity: 1;
        }
        font-size: 20px;
        cursor: pointer;
      }
    }

    &-dialogs {
      height: calc(100%-136px);
      .dialogs {
        height: 100%;
        overflow: auto;
        &__item {
          padding: 5px 20px;
          margin-bottom: 0;
          cursor: pointer;
          &:active,
          &:hover {
            background-color: #f3f7ff;
          }
        }
      }
    }
  }

  &__dialog {
    flex: 1;

    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 12px;
      border-bottom: 1px solid #ecebeb;

      &-center {
        text-align: center;
      }

      &-username {
        display: block;
        margin-bottom: 5px;
        font-weight: bold;
      }

      span.anticon {
        transition: 0.5s ease;
        opacity: 0.5;
        &:hover {
          opacity: 1;
        }
        font-size: 20px;
        cursor: pointer;
      }
    }

    &-messages {
      height: calc(100% - 140px);
    }
  }
}
