@import "../../styles/variables";

.message {
  $self: &;

  &__content {
    display: flex;
    max-width: 440px;
    margin-bottom: 7px;
    position: relative;
  }

  &__avatar {
    display: flex;
    flex-direction: column-reverse;
    .avatar {
      border-radius: 50px;
      width: 33px;
      height: 33px;
      margin-right: 13px;
      margin-bottom: 23px;
      font-size: 17px;
    }
  }

  &__bubble {
    background: $blue;
    box-shadow: 0px 5px 5px rgba(54, 116, 255, 0.2);
    border-radius: 12px;
    padding: 15px;
    margin-bottom: 8px;
  }

  &__date {
    font-size: 12px;
    opacity: 0.5;
  }

  &__text {
    color: #fff;
    line-height: 20px;
  }

  &__attachments {
    display: flex;
    &-item {
      margin-right: 10px;
      cursor: pointer;
      img {
        width: 50px;
        height: 50px;
        border-radius: 9px;
      }
    }
  }

  &__icon {
    &-readed {
      position: absolute;
      bottom: 23px;
      left: -23px;

      &--no {
        left: -20px;
      }
    }
    &-actions {
      cursor: pointer;
      transform: rotate(90deg);
      position: absolute;
      left: -26px;
      top: 0;
    }
  }

  &--isme {
    display: flex;
    flex-direction: row-reverse;

    .avatar {
      margin-left: 13px;
      margin-right: 0;
    }

    #{$self}__content {
      margin-bottom: 15px;
      flex-direction: row-reverse;
    }

    #{$self}__avatar {
      img {
        margin-right: 0;
        margin-left: 13px;
      }
    }

    #{$self}__info {
      text-align: right;
    }

    #{$self}__buble {
      background: #fff;
      border: 1px solid #ececec;
      box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
      #{$self}__text {
        color: $black;
      }
    }
  }

  &--image {
    #{$self}__attachments {
      &-item {
        img {
          width: 150px;
          height: 150px;
        }
      }
    }
  }

  &__typing {
    span {
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: #fff;
      display: inline-block;
      margin-right: 8px;

      &:nth-of-type(1) {
        animation: cycleOne 1s ease-in-out infinite;
        animation-direction: normal;
      }
      &:nth-of-type(2) {
        animation: cycleTwo 1s ease-in-out infinite;
        animation-direction: normal;
      }
      &:nth-of-type(3) {
        animation: cycleThree 1s ease-in-out infinite;
        animation-direction: normal;
      }
    }
  }
}

@keyframes cycleOne {
  0% {
    opacity: 0.4;
  }
  33.333% {
    opacity: 1;
  }
  66.6667% {
    opacity: 0.4;
  }
  100% {
    opacity: 0.4;
  }
}

@keyframes cycleTwo {
  0% {
    opacity: 0.4;
  }
  33.333% {
    opacity: 0.4;
  }
  66.6667% {
    opacity: 1;
  }
  100% {
    opacity: 0.4;
  }
}

@keyframes cycleThree {
  0% {
    opacity: 0.4;
  }
  33.333% {
    opacity: 0.4;
  }
  66.6667% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
