.chat-input {
  display: flex;
  margin: 10px;
  height: 45px;

  &__smile-btn {
    span.anticon {
      margin: 15px;
    }
  }
  &__emoji-picker {
    position: absolute;
    bottom: 85px;
  }

  span.anticon {
    transition: 0.5s ease;
    opacity: 0.5;
    &:hover {
      opacity: 1;
    }
    font-size: 20px;
    cursor: pointer;
  }

  &__actions {
    display: flex;
    align-items: center;
    width: 90px;
    justify-content: space-between;
    margin-left: 15px;
    margin-right: 15px;

    &-upload-btn {
      ::-webkit-file-upload-button {
        cursor: pointer;
      }
      &:hover {
        span {
          opacity: 1;
        }
      }
    }
  }

  input {
    &:focus,
    &:hover {
      box-shadow: none;
      border-color: #ecebeb;
    }
  }
}
